.h1 {
    font-size: calc(20px + 5vw) !important;
}
#footer {
    font-size: calc(10px + .5vw) !important;
}
.hide {
    display: none;
}
.alert-body {
    opacity: 100% !important;
    position: fixed;
    top: 20%;
    left: 25%;
    padding-top: 5% !important;
    padding-bottom: 5% !important;
    padding-left: 3% !important;
    padding-right: 3% !important;
    height: 60%;
    width: 50%;
    z-index: 3;
}
#overlay {
    opacity: 80% !important;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    z-index: 2;
}
.board {
    min-width: 300px !important;
}
.boardblock {
    z-index: 1;
    height: 5vw;
    min-height: 75px;
}
.boardcell {
    z-index: 0;
    cursor: pointer;
}
.boardcell:hover {
    background-color: #efefef;
}
.boardrow {
    height: 5vw;
    min-height: 75px !important;
}
.winningcell {
    background-color: #f7b5b5 !important;
}
.winningcell:hover {
    background-color: #e6a1a1 !important;
}

/* fourblock rounding */
.rounded-top-left {
    border-top-left-radius: 4px;
}
.rounded-bottom-left {
    border-bottom-left-radius: 4px;
}
.rounded-top-right {
    border-top-right-radius: 4px;
}
.rounded-bottom-right {
    border-bottom-right-radius: 4px;
}

/* draggable elements */
.draggable {
    cursor: pointer;
}
.draggable-oneblock {
    height: 3vw !important;
    min-height: 40px;
    width: 3vw !important;
    min-width: 40px;
    z-index: 1;
}
.draggable-oneblock:hover {
    background-color: #408f40 !important;
}
.draggable-twoblockH {
    height: 3vw !important;
    min-height: 40px;
    width: 6vw !important;
    min-width: 80px;
    z-index: 1;
}
.draggable-twoblockH:hover {
    background-color: #f0ad4e !important;
}
.draggable-twoblockV {
    height: 6vw !important;
    min-height: 80px;
    width: 3vw !important;
    min-width: 40px;
    z-index: 1;
}
.draggable-twoblockV:hover {
    background-color: #0275d8 !important;
}
.draggable-fourblock {
    height: 6vw !important;
    min-height: 80px;
    width: 6vw !important;
    min-width: 80px;
    z-index: 1;
}
.draggable-fourblock:hover {
    background-color: #cf4a46 !important;
}